* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 16px;
}
header {
  height: 100vh;
  background-color: white;
}
nav {
  width: 70%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  height: 100px;
  align-items: center;
  border-bottom: 1px solid hsl(220, 14%, 75%);
  .nav-left {
    display: flex;
    align-items: center;
    .logo {
      font-weight: 700;
      color: hsl(220, 13%, 13%);
      font-size: 40px;
      margin-right: 60px;
    }
    ul {
      display: inline-block;
      line-height: 100%;
      color: hsl(219, 9%, 45%);
      li {
        list-style-type: none;
        display: inline-block;
        margin-right: 30px;
        padding: 39px 0px;
        border-bottom: 5px solid white;
        cursor: pointer;

        &:hover {
          color: hsl(220, 14%, 75%);
          border-bottom: 5px solid hsl(26, 100%, 55%);
          transition: all 0.5s;
        }
      }
    }
  }
  .nav-right {
    display: flex;
    align-items: center;
    position: relative;

    .cart {
      position: absolute;
      top: calc(100% + 20px);
      right: -50%;
      width: 400px;
      box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
      background-color: white;

      h2 {
        padding: 20px 0 50px 30px;
        border-bottom: 1px solid hsl(220, 14%, 75%);
      }

      .cart-detail {
        padding: 20px 0 50px 30px;
        display: flex;

        img {
          margin-right: 10px;
        }

        .cart-detail__content {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }
      }
    }

    .nav-right__icon {
      position: relative;
      margin-right: 45px;
      cursor: pointer;

      span {
        position: absolute;
        top: -50%;
        padding: 2px 4px;
        border-radius: 50%;
        right: -50%;
        color: white;
        background-color: hsl(26, 100%, 55%);
      }
    }

    img {
      width: 50px;
      height: auto;
    }
  }
}

.product-active {
  filter: grayscale(0.8);

  border: 2px solid hsl(26, 100%, 55%);
}
main {
  width: 70%;
  height: 80vh;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

  .main-left {
    width: 40%;
    align-self: center;

    img {
      border-radius: 10px;
    }

    .main-img {
      width: 100%;
    }

    .box-img {
      display: flex;
      margin-top: 30px;
      justify-content: space-between;
      img {
        width: 20%;
      }
    }
  }
  .main-right {
    width: 40%;
    align-self: center;

    .head {
      text-transform: uppercase;
      color: hsl(26, 100%, 55%);
      margin-bottom: 20px;
    }
    .title {
      font-weight: 700;
      font-size: 40px;
      margin-bottom: 40px;
    }
    .text {
      color: hsl(220, 14%, 75%);
      margin-bottom: 30px;
    }
    .price {
      font-weight: 700;
      margin-bottom: 40px;
      font-size: 30px;
      display: flex;
      align-items: center;

      .sale {
        display: inline-block;
        background-color: hsl(25, 100%, 94%);
        color: hsl(26, 100%, 55%);
        font-size: 16px;
        border-radius: 20%;
        padding: 4px 8px;
        margin-left: 30px;
      }
    }

    .quantityBox {
      display: flex;
      .quantity {
        width: 150px;
        height: 50px;
        background-color: hsl(223, 64%, 98%);
        margin-right: 15px;
        border-radius: 7px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        > span:first-child,
        span:last-child {
          color: hsl(26, 100%, 55%);
          cursor: pointer;
        }
        span {
          font-size: 25px;
        }
      }
      .button-add {
        // height: 100%;
        padding: 10px 50px;
        border-radius: 10px;
        background-color: hsl(26, 100%, 55%);
        text-decoration: none;
        color: white;
      }
    }
  }
}
